import Vue from "vue";
import FDialog from "../components/FDialog";
const DialogConstructor = Vue.extend(FDialog);
function install(Vue, vuetify) {
    let instance = null;
    const queue = [];
    const create = (options) => {
        const instance = new DialogConstructor();
        const app = document.querySelector("[data-app]");
        Object.assign(instance, options);
        instance.$vuetify = vuetify.framework;
        app?.appendChild(instance.$mount().$el);
        return instance;
    };
    const show = (options) => {
        if (instance) {
            instance.close();
            queue.unshift(options);
            return;
        }
        instance = create(options);
        instance.show(options);
        instance.$on("close", close);
    };
    const close = () => {
        instance.$nextTick(() => {
            instance.$destroy();
            instance.$el.parentNode.removeChild(instance.$el);
            instance = null;
            if (queue.length) {
                const options = queue.shift();
                options && show(options);
            }
        });
    };
    const shorts = () => {
        const types = ["warning"];
        const methods = {};
        types.forEach((t) => {
            methods[t] = (options) => show({ type: t, ...options });
        });
        return methods;
    };
    const clear = () => queue.splice(0, queue.length);
    Vue.prototype.$uikit = Vue.prototype.$uikit || {};
    Vue.prototype.$uikit.dialog = {
        show,
        clear,
        ...shorts()
    };
}
function Dialog() { }
Dialog.install = install;
export default Dialog;
